import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import dayjs from "dayjs";
import axios from "axios";
import Navbar from "../Navbar.jsx";
import Sidebar from "../Sidebar.jsx";
import Loading from "../../Loading.jsx";
import PackLotModal from "./PackLotModal.jsx";

export default function RawLotList() {
  /* ─── auth header once ─────────────────────────────────────────── */
  const token = localStorage.getItem("token") || "";
  const auth = { headers: { Authorization: `Bearer ${token}` } };

  /* ─── state ────────────────────────────────────────────────────── */
  const [lots, setLots] = useState([]);
  const [busy, setBusy] = useState(true);
  const [openOnly, setOpenOnly] = useState(true);
  const [modal, setModal] = useState({ open: false, lot: null });
  const [sidebarOpen, setSidebarOpen] = useState(window.innerWidth >= 768);

  /* ─── fetch lots (+pack summary) ───────────────────────────────── */
  useEffect(() => {
    (async () => {
      try {
        setBusy(true);
        const { data } = await axios.get("/api/raw-lots", {
          ...auth,
          params: {
            packed: openOnly ? 0 : undefined,
            withPacks: 1, // Adds packsMade / packsLeft
          },
        });
        setLots(data.data || []);
      } catch (err) {
        alert(err.response?.data?.message || err.message);
        setLots([]);
      } finally {
        setBusy(false);
      }
    })();
  }, [openOnly]);

  /* helper: keep table fresh after a PATCH ------------------------- */
  const replaceLot = (updated) =>
    setLots((prev) => prev.map((l) => (l._id === updated._id ? updated : l)));

  /* ─── UI ───────────────────────────────────────────────────────── */
  if (busy) return <Loading />;

  return (
    <div className="flex h-screen">
      <Sidebar isSidebarOpen={sidebarOpen} />

      <div className="flex flex-1 flex-col">
        <Navbar isSidebarOpen={sidebarOpen} setSidebarOpen={setSidebarOpen} />

        <header className="flex items-center justify-between bg-gray-100 px-4 py-3">
          <h1 className="text-xl font-semibold">Raw Lots</h1>

          <label className="flex items-center gap-2 text-sm cursor-pointer">
            <input
              type="checkbox"
              className="accent-cyan-600"
              checked={openOnly}
              onChange={(e) => setOpenOnly(e.target.checked)}
            />
            Show only <em>open</em> lots
          </label>
        </header>

        {lots.length === 0 ? (
          <p className="p-6 text-center text-gray-500">No raw lots found.</p>
        ) : (
          <div className="overflow-auto p-4">
            <table className="w-full min-w-[1100px] border text-sm"> {/* Adjusted min-width */}
              <thead className="bg-sky-600 text-white">
                <tr>
                  {[
                    "Date",
                    "Item",
                    "Bulk Qty",
                    "Bulk Cost (₹)",
                    "Pack Size",
                    "Pack Cost",
                    "Packs Made",
                    "Packs Left",
                    "Warehouse",
                    "Status",
                    "Action",
                  ].map((h) => (
                    <th key={h} className="border p-2">
                      {h}
                    </th>
                  ))}
                </tr>
              </thead>

              <tbody>
                {lots.map((lot) => {
                  /* derived helpers ---------------------------------- */
                  const unitCost = lot.unitCost ?? lot.bulkCost / lot.bulkQty;
                  const packsMade = lot.packsMade || 0;
                  const packCost =
                    lot.packSize && unitCost
                      ? lot.isPacked && packsMade > 0
                        ? (unitCost * (lot.bulkQty / packsMade)).toFixed(2) // Adjusted cost
                        : (unitCost * lot.packSize).toFixed(2)
                      : null;
                  const statusTxt = lot.packSize
                    ? lot.isPacked
                      ? "Packed"
                      : "Partial"
                    : "Open";

                  /* -------------------------------------------------- */
                  return (
                    <tr key={lot._id} className="text-center">
                      <td className="border p-2">
                        {dayjs(lot.createdAt).format("DD-MMM-YY")}
                      </td>
                      <td className="border p-2">{lot.item?.itemName || "—"}</td>
                      <td className="border p-2">
                        {lot.bulkQty} {lot.bulkUnit}
                      </td>
                      <td className="border p-2">{lot.bulkCost.toFixed(2)}</td>
                      <td className="border p-2">
                        {lot.packSize ? `${lot.packSize.toFixed(4)} ${lot.bulkUnit}` : "—"}
                      </td>
                      <td className="border p-2">{packCost ?? "—"}</td>
                      <td className="border p-2">{packsMade ?? "—"}</td>
                      <td className="border p-2">{lot.packsLeft ?? "—"}</td>
                      <td className="border p-2">
                        {lot.warehouse?.warehouseName || "—"}
                      </td>
                      <td className="border p-2">{statusTxt}</td>
                      <td className="border p-2">
                        {lot.packSize == null ? (
                          <Link
                            to="#"
                            className="text-blue-600 hover:text-blue-800"
                            onClick={() => setModal({ open: true, lot })}
                          >
                            Define Pack
                          </Link>
                        ) : lot.packsLeft > 0 || !lot.isPacked ? (
                          <Link
                            to="#"
                            className="text-blue-600 hover:text-blue-800"
                            onClick={() => setModal({ open: true, lot })}
                          >
                            Pack More
                          </Link>
                        ) : (
                          <span className="text-gray-400">Done</span>
                        )}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        )}

        {/* modal ----------------------------------------------------- */}
        <PackLotModal
          open={modal.open}
          lot={modal.lot}
          onClose={() => setModal({ open: false, lot: null })}
          onSave={async ({ packSize, packs, isFullyPacked }) => {
            try {
              const { data } = await axios.patch(
                `/api/raw-lots/${modal.lot._id}/pack`,
                { packSize, packs, isFullyPacked },
                auth
              );
              replaceLot(data.data); // Keep UI synced
              setModal({ open: false, lot: null });
            } catch (err) {
              alert(err.response?.data?.message || err.message);
            }
          }}
        />
      </div>
    </div>
  );
}