import React, { useState, useEffect } from "react";

export default function PackLotModal({ open, onClose, onSave, lot }) {
  // ── 1) Hooks always run first ───────────────────
  const [packSize, setPackSize]           = useState(lot?.packSize ?? "");
  const [packsToMake, setPacks]           = useState("");
  const [isFullyPacked, setIsFullyPacked] = useState(false);

  useEffect(() => {
    // reset whenever a new lot is opened
    if (lot) {
      setPackSize(lot.packSize ?? "");
      setPacks("");
      setIsFullyPacked(false);
    }
  }, [lot]);

  // ── 2) Then guard the render ───────────────────
  if (!open || !lot) return null;

  // ── 3) Derived values ───────────────────────────
  const totalPossible =
    (lot.packSize || packSize) > 0
      ? Math.floor(lot.bulkQty / (lot.packSize || packSize))
      : 0;
  const made =
    lot.packSize ? Math.floor((lot.packedQty || 0) / lot.packSize) : 0;
  const packsLeft = totalPossible - made;

  const canSave =
    (lot.packSize || packSize) > 0 &&
    packsToMake > 0 &&
    (isFullyPacked || packsToMake <= packsLeft);

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div className="w-96 rounded bg-white p-6 shadow-lg space-y-4">
        <h2 className="text-lg font-semibold text-center">
          Pack “{lot.item?.itemName}”
        </h2>

        {!lot.packSize ? (
          <label className="block text-sm">
            Pack size (in {lot.bulkUnit}):
            <input
              type="number"
              min="0.001"
              step="0.001"
              value={packSize}
              onChange={(e) => setPackSize(+e.target.value)}
              className="mt-1 w-full rounded border px-2 py-1"
              autoFocus
            />
          </label>
        ) : (
          <p className="text-sm">
            Pack size:{" "}
            <span className="font-medium">
              {lot.packSize} {lot.bulkUnit}
            </span>
          </p>
        )}

        <label className="block text-sm">
          Packs to make{" "}
          <span className="text-gray-500">(max {packsLeft})</span>
          <input
            type="number"
            min="1"
            step="1"
            value={packsToMake}
            onChange={(e) => setPacks(+e.target.value)}
            className="mt-1 w-full rounded border px-2 py-1"
          />
        </label>

        <label className="flex items-center gap-2 text-sm">
          <input
            type="checkbox"
            checked={isFullyPacked}
            onChange={(e) => setIsFullyPacked(e.target.checked)}
            className="accent-blue-600"
          />
          Mark as fully packed
        </label>

        <div className="flex justify-end gap-3 pt-2">
          <button
            className="rounded bg-gray-300 px-4 py-1 hover:bg-gray-400"
            onClick={onClose}
          >
            Cancel
          </button>
          <button
            className="rounded bg-blue-600 px-4 py-1 text-white hover:bg-blue-700 disabled:opacity-50"
            disabled={!canSave}
            onClick={() =>
              onSave({
                packSize: lot.packSize ?? +packSize,
                packs: +packsToMake,
                isFullyPacked,
              })
            }
          >
            Save
          </button>
        </div>
      </div>
    </div>
  );
}
